::-webkit-file-upload-button { -webkit-appearance: button; /* 1 */ font: inherit; /* 2 */ }

/* Interactive ========================================================================== */
/* Add the correct display in IE 9-. 1. Add the correct display in Edge, IE, and Firefox. */
details, menu { display: block; }

/* Add the correct display in all browsers. */
summary { display: list-item; }

/* Scripting ========================================================================== */
/** Add the correct display in IE 9-. */
canvas { display: inline-block; }

/** Add the correct display in IE. */
template { display: none; }

/* Hidden ========================================================================== */
/** Add the correct display in IE 10-. */
[hidden] { display: none; }

/** Set up a decent box model on the root element */
html { box-sizing: border-box; }

/** Make all elements from the DOM inherit from the parent box-sizing Since `*` has a specificity of 0, it does not override the `html` value making all elements inheriting from the root box-sizing value See: https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
*, *::before, *::after { box-sizing: inherit; }

/** Basic styles for links */
a { color: #222222; text-decoration: none; }

a:hover, a:active, a:focus { color: #a2a2a2; }

/** Basic typography style for copy text */
body, html { color: #222222; font-family: "Helvetica Neue Light", "Helvetica Neue", "Helvetica", "Arial", sans-serif; }

h1, h2, h3, h4, h5, h6 { font-family: "Fjalla One", "Helvetica Neue Light", "Helvetica Neue", "Helvetica", "Arial", sans-serif; }

/** Clear inner floats */
.clearfix::after { clear: both; content: ''; display: table; }

/** Main content containers 1. Make the container full-width with a maximum width 2. Center it in the viewport 3. Leave some space on the edges, especially valuable on small screens */
.container { max-width: 1180px; /* 1 */ margin-left: auto; /* 2 */ margin-right: auto; /* 2 */ padding-left: 20px; /* 3 */ padding-right: 20px; /* 3 */ width: 100%; /* 1 */ }

/** Hide text while making it readable for screen readers 1. Needed in WebKit-based browsers because of an implementation bug; See: https://code.google.com/p/chromium/issues/detail?id=457146 */
.hide-text { overflow: hidden; padding: 0; /* 1 */ text-indent: 101%; white-space: nowrap; }

/** Hide element while making it readable for screen readers Shamelessly borrowed from HTML5Boilerplate: https://github.com/h5bp/html5-boilerplate/blob/master/src/css/main.css#L119-L133 */
.visually-hidden { border: 0; clip: rect(0 0 0 0); height: 1px; margin: -1px; overflow: hidden; padding: 0; position: absolute; width: 1px; }

header { width: 100%; padding: 1rem 0; overflow: hidden; position: relative; }

header h1 { margin-top: 0; margin-bottom: 0; font-weight: 300; font-size: 2.5rem; }

header .site-title img { background: #333; }

header nav ul { padding: 0; margin: 0; }

header nav .menu li { list-style: none; display: inline-block; }

header nav .menu li a { font-weight: 300; padding: 1rem 1rem; margin: 0; }

header.pages .site-title img { height: 48px; }

header.pages nav ul { float: right; text-align: right; }

header.landing { height: 95vh; background: rgba(255, 255, 255, 0.95); margin-bottom: 3px; }

header.landing:before { position: absolute; bottom: 0; height: 95vh; width: 100vw; background-image: url("./dist/imgs/header.jpg"); background-repeat: none; background-attachment: fixed; background-position: center center; background-size: cover; z-index: -1; }

header.landing .site-title { text-align: center; }

header.landing .site-title img { margin-top: 10vh; height: 50vh; margin-bottom: 1rem; }

header.landing nav { text-align: center; }

header.landing nav .menu li a { font-weight: 300; font-size: 3vh; transition: .25s; color: #222222; }

header.landing nav .menu li a:hover, header.landing nav .menu li a:active, header.landing nav .menu li a:focus { background: #222222; color: #fff; }

.screen-reader-text { display: none; }

footer { text-align: center; font-weight: 400; font-size: .65rem; margin: 1rem auto 0; }

footer .footer-info { margin-bottom: .5rem; }

footer .footer-info:last-child { margin-bottom: 1rem; }

footer .footer-info span { margin-right: 1rem; }

footer .footer-info span:first-child { font-weight: 700; text-transform: uppercase; }

footer .footer-info span:last-child { margin-right: 0; }

footer .contact-info { font-size: .75rem; }

footer a { color: #666; }

main.site-main { padding: 0; }

main.site-main #freewall { padding-bottom: 3px; }

main.site-main .brick { position: relative; }

main.site-main .brick img { width: 100%; }

main.site-main .brick .paint { transition: opacity .2s ease-in-out; position: absolute; bottom: 0; font-weight: 700; font-size: 1.5rem; color: #fff; background: rgba(255, 255, 255, 0.8); height: 100%; width: 100%; vertical-align: middle; padding: .5rem; opacity: 0; }

main.site-main .brick .paint title { transition: .3s; position: absolute; bottom: -1rem; left: 1rem; width: 200px; color: #222222; display: block; }

main.site-main .brick a:hover .paint, main.site-main .brick a:active .paint, main.site-main .brick a:focus .paint { opacity: 1; }

main.site-main .brick a:hover .paint title, main.site-main .brick a:active .paint title, main.site-main .brick a:focus .paint title { bottom: 1rem; }

main.project-main .banner { height: 70vh; background: rgba(255, 255, 255, 0.85); position: relative; }

main.project-main .banner h2 { position: absolute; bottom: 1rem; left: 1rem; font-size: 3rem; }

main.project-main .banner .banner-background { position: absolute; bottom: 0; height: 70vh; width: 100%; background-image: url("./dist/imgs/header.jpg"); background-repeat: none; background-attachment: fixed; background-position: center center; background-size: cover; z-index: -1; display: block; }

main.project-main .content { margin-top: 1rem; }
